<template>
    <footer class="footer">
        <div class="vice-nav">
            <ul>
                <li class="web-information">© 2018 原点·Oplace</li>
                <li v-for="(item, index) in viceNavs" :key="index">
                    <a href="javascript:;">{{ item }}</a>
                </li>
            </ul>
        </div>
        <div class="contact-us">
            <p>联系我们：</p>
            <a class="weixin" @click.prevent="flag=!flag"></a>
            <a href="" class="weibo"></a>
        </div>

        <!-- 弹出框 -->
        <popup class="QR-popup" v-if="flag">
            <div slot="content" class="QR-content">
                <h2 class="QR-code-title">扫一扫，关注「原点·Oplace」微信公众号</h2>
                <span class="QR"><img src="static/images_icon/QR.png" alt="扫一扫微信二维码"></span>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="flag=false" />
        </popup>
    </footer>
</template>

<script>
    import Popup from '../component/popup.vue';
    export default {
        data() {
            return {
                flag: false,
                viceNavs: [ "版权声明", "服务条款", "隐私政策", "关于我们", "API" ], // 底部导航
            }
        },
        methods: {
        },
        components: {
            Popup
        }
    }
</script>

<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    
    /*footer部分*/
    .footer {
        width: 100%;
        height: 54px;
        background-color: #ffffff;
        border-top: 1px solid #e8e8e8;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        margin-top: 50px;
    }

    /*副标题*/
    .vice-nav {
        height: 100%;
        width: auto;
        float: left;
        padding: {
            top: 18px;
            left: 38px;
        }
        ul > li {
            float: left;
            margin-right: 26px;
            height: 18px;
            font-size: 12px;
            line-height: 18px;
            color: #808080;
            font-weight: normal;
            a {
                color: #808080;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }

    /*联系我们*/
    .contact-us {
        height: 100%;
        width: auto;
        float: right;
        padding: {
            top: 16px;
            right: 34px;
        }
        p {
            width: auto;
            height: 18px;
            line-height: 18px;
            font-size: 12px;
            color: #808080;
            float: left;
            padding-top: 4px;
            margin-right: 20px;
        }
        a {
            width: 22px;
            height: 22px;
            float: left;
            background: url(../../../static/images_icon/sprite.png) no-repeat;
            background-position: -227px -40px;
            background-size: auto 290px;
        }
        a.weixin {
            background-position: -227px -40px;
            margin-right: 24px;
            margin-top: 1px;
            &:hover {
                background-position: -227px 2px;
            }
        }
        a.weibo {
            background-position: -172px -264px;
            &:hover {
                background-position: -172px -225px;
            }
        }
    }

    // 二维码弹框
    .QR-content {
        width: 500px;
        height: 340px;
    }
    .QR-code-title {
        font-size: 16px;
        color: #343434;
        text-align: center;
        margin-top: 50px;
    }

    .QR {
        display: block;
        width: 219px;
        height: 219px;
        margin: 46px auto 80px;
    }
</style>


