<template>
    <popup class="exit-popup">
        <div slot="content" class="content">
            <h3>确定要退出登录</h3>
            <div class="buttons">
                <o-button :text="'取消'" :classStyle="'del-btn'" class="btn" @btnClick="cancleLogin"></o-button>
                <o-button :text="'确定'" :classStyle="'main-btn'" class="btn" @btnClick="sureLogin"></o-button>
            </div>
        </div>
        <input type="button" name="" class="close-btn" slot="close" @click="closePopup" />
    </popup>
</template>
<script>
    import Popup from '../../component/popup.vue';
    import OButton from '../../component/button.vue'
    export default {
        data () {
            return {

            }
        },
        created () {
        },
        methods: {

            // 关闭弹窗
            closePopup () {
                this.$emit('exitLogon');
            },

            // 点击取消
            cancleLogin () {
                this.$emit('exitLogon');
            },

            // 点击确定
            sureLogin () {
                this.$emit('exitLogon');
                this.$store.commit("ISLOGIN", !this.$store.state.isLogin);
            }
        },
        components: {
            Popup,
            OButton
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>
    .exit-popup {

        .content {
            padding: 30px 20px;
            text-align: center;
            overflow: hidden;
            .buttons {
                margin: 0 auto;
                .btn {
                    width: 57px;
                    height: 30px;
                    float: left;
                    margin-top: 30px;
                }
                .main-btn {
                    margin-left: 10px;
                }
            }
        }
    }
</style>


