<template>
    <popup v-if="closeFlag">
        <h2 slot="header" class="login-box-bg" ref="temp">
            <img src="static/images_icon/login_box_bg.png" alt="" />
            <span class="close" @click="close"></span>
        </h2>
        <form slot="content" class="form-content login-popup">
            <div class="from-control" v-if="codeFlag || passwordFlag">
                <label class="label">手机号</label>
                <input type="number" name="tel" v-model.number="formdata.tel" value="" placeholder="请输入手机号码" class="form-input" />
            </div>
            <div class="from-control" v-if="codeFlag">
                <label class="label">验证码</label>
                <span class="v-code-box">
                    <input type="number" placeholder="请输入验证码" name="code" v-model.number="formdata.code" class="form-input v-code">
                    <o-button :text="'获取验证码'" :classStyle="'main-btn'" class="v-code-btn"></o-button>
                </span>
            </div>
            <div class="from-control" v-if="passwordFlag">
                <label class="label">密码</label>
                <input type="password" name="password" v-model.trim="formdata.password" value="" placeholder="请输入密码" class="form-input" />
            </div>
            <div class="from-control" v-if="setPassFlag">
                <label class="label">设置密码</label>
                <input type="password" name="password" v-model.trim="formdata.setPassword" value="" placeholder="请输入密码" class="form-input" />
            </div>
            <o-button :inputType="'button'" :text="'登录 / 注册'" :classStyle="'main-btn'" class="go-login" v-if="codeFlag" @btnClick="submitForm"></o-button>
            <o-button :text="'登录'" :classStyle="'main-btn'" class="go-login"  v-if="passwordFlag"></o-button>
            <o-button :text="'确定'" :classStyle="'main-btn'" class="go-login" v-if="setPassFlag"></o-button>
        </form>
        <div class="footer" slot="footer">
            <a href="javascript:;" v-if="codeFlag" @click.prevent="showPasswordPopup">
                密码登录
                <span class="arrowR"></span>
            </a>
            <a href="javascript:;" v-if="passwordFlag" @click.prevent="showPasswordPopup">
                验证码登录
                <span class="arrowR"></span>
            </a>
            <a class="back" v-if="setPassFlag" @click.prevent="showCodePopup">
                <span class="arrowR arrowL"><</span>
                返回
            </a>
        </div>
    </popup>
</template>
<script>
    import Popup from '../../component/popup.vue';
    import OButton from '../../component/button.vue';
    export default {
        name: "Login",
        data() {
            return {
                closeFlag: true, // 弹窗显示标志
                codeFlag: true, // 验证码显示标志
                passwordFlag: false, // 密码框显示标志
                setPassFlag: false, // 设置密码框显示标志
                formdata: { // 表单数据
                    tel: '', // 手机号码
                    code: '', // 验证码
                    password: '', // 密码
                    setPassword: '' // 设置密码
                },
                rules: {
                    tel: {
                        required: true, message: '请输入手机号码', trigger: 'blur'
                    },
                    code: {
                        required: true, message: '请输入验证码', trigger: 'blur'
                    }
                },
                isLogin: this.$store.state.isLogin, // 是否登录
            }
        },
        mounted() {
            // this.showLogin = true;
        },
        computed: {
            // user() {
            //     return this.$store.state.user
            // }
            // ...mapState(['isLogin'])
        },
        methods: {

            // 显示使用密码登录框
            showPasswordPopup() {
                this.codeFlag = !this.codeFlag;
                this.passwordFlag = !this.passwordFlag;
            },

            // 显示使用验证码登录框
            showCodePopup() {
                this.codeFlag = !this.codeFlag;
                this.passwordFlag = false;
            },

            // 点击关闭按钮 关闭弹框
            close() {
                this.closeFlag = !this.closeFlag;
                this.$emit('closePopup', this.closeFlag);
            },

            // 点击登录按钮 发送数据 显示登录成功
            submitForm() {
                this.isLogin = !this.isLogin;
                console.log(this.isLogin);
                localStorage.setItem('isLogin', this.isLogin);
                this.$store.commit('ISLOGIN', this.isLogin);
                this.close();
            }
        },
        props: {
            
        },
        components: {
            Popup,
            OButton
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>

    .login-popup {
        width: 454px;
    }

    /*登录弹框*/
    .login-box-bg {
        width: 454px;
        height: 180px;
        border-radius: 5px 5px 0 0;
        .close {
            width: 40px;
            height: 40px;
            background: url(/static/images_icon/sprite.png) no-repeat;
            background-position: -120px -142px;
            background-size: auto 300px;
            position: absolute;
            top: 0;
            right: 0;
            z-index: 2;
            cursor: pointer;
            opacity: 0.65;
            &:hover {
                opacity: 1;
                animation: spin 900ms 1 linear;
            }
        }
    }
    .go-login {
        width: 158px;
        height: 40px;
        margin: 30px auto;
        font-size: 15px !important;
        font-weight: bold;
    }
    .form-content {
        display: block;
        width: 284px;
        height: auto;
        margin: 0 auto;
        padding-top: 30px;
        .from-control {
            width: 100%;
            height: 36px;
            line-height: 36px;
            margin-bottom: 10px;
            .label {
                display: block;
                float: left;
                font-size: 13px;
                font-weight: bold;
                height: 100%;
                line-height: 37px;
            }
            .form-input {
                display: block;
                width: 225px;
                height: 100%;
                border-radius: 4px;
                float: right;
                background-color: #F4F4F4;
                padding-left: 6px;
            }
            .v-code-box {
                display: block;
                width: 225px;
                height: 100%;
                float: right;
                .v-code {
                    width: 128px;
                    float: left;
                }
                .v-code-btn {
                    width: 91px;
                    height: 100%;
                }
            }
        }
    }
    .footer {
        color: #48abff;
        text-align: right;
        height: auto;
        padding: 0 30px;
        font-size: small;
        >a {
            height: 40px;
            line-height: 40px;
            color: #48abff;
        }
        a.back {
            text-align: left;
        }
        .arrowR, .arrowL {
            display: inline-block;
            width: 13px;
            height: 13px; 
            background: url(/static/images_icon/sprite.png) no-repeat #48abff;
            background-position: -67px 2px;
            background-size: auto 300px;
            border-radius: 50%;
            transform: rotate(90deg);
            vertical-align: middle;
        }
        .arrowL {
            transform: rotate(-90deg);
        }
    }
    
    // 关闭按钮选择动画
    @keyframes spin {
        0% {
            transform: rotate(360deg);
        }
        100% {
            transform: rotate(0deg);
        }
    }


</style>


