<template>
    <header class="header">
        <div class="container">
            <h1 class="logo">
                <a href="index.html"><img src="static/images_icon/logo.png" alt="原点·Oplace" /></a>
            </h1>
            <div class="center">
                <nav class="top-nav">
                    <ul class="list">
                        <li v-for="(item, index) in topNavList" :key="index" class="item">
                            <router-link :to="item.navHref" tag="a">{{ item.text }}</router-link>
                        </li>
                    </ul>
                </nav>
                <div class="search-wrap">
                    <input type="button" name="" readonly="readonly" class="search-close" v-show="flag" @click="closeSearch" />
                    <input type="button" readonly="readonly" class="search-icon" :class="{ 'search-click':searchFlag }" @click="showSearch" />
                    <input type="text" placeholder="请输入您想要搜索的内容" class="search-input" v-show="flag" />
                </div>
            </div>

            <!-- 未登录时显示的登录/注册按钮-->
            <a class="login-or-register" @click.prevent="showLogin" v-show="!$store.state.isLogin">登陆 / 注册</a>

            <!-- 登录后的个人头像/账户管理菜单/消息列表-->
            <div class="account-box" v-show="$store.state.isLogin">
                <span class="account-head-box">
                    <a href="javascript:;" class="account-head">
                        <img src="static/images_header/header3.jpg" />

                        <!-- 个人中心 -->
                        <div class="account-option hover-box">
                            <ul class="list">
                                <li class="item" v-for="(value, index) in options" :key="index">
                                    <a href="javascript:;" @click.prevent="value.event">
                                        <span class="icon" :class="value.classType"></span>
                                        <p class="title">{{ value.title }}</p>
                                    </a>
                                </li>
                            </ul>
                            <a href="javascript:;" class="bottom-btn" @click.prevent="exitLogon">退出登录</a>
                        </div>

                        <!--完善资料提示-->
                        <div class="perfect-information-tip hover-box">
                            <p class="perfect-information-text">进一步完善您的资料，获得折扣券</p>
                        </div>
                    </a>
                </span>
                <a href="javascript:;" class="account-type"></a>
                <a href="javascript:;" class="message" :class="{'no-message': messages.length === 0 }">

                    <!-- 消息中心 -->
                    <div class="message-box hover-box">

                        <!-- 有消息 -->
                        <div v-if="messages.length !== 0">
                            <ul class="list">
                                <li class="item" v-for="(value, index) in messages" :key="index">
                                    <a href="javascript:;" class="message-author-box">

                                        <!-- 是未读消息时，显示红色圆点标记 -->
                                        <span class="unread" v-if="value.reading"></span>

                                        <span class="sender-head">
                                            <img :src="value.senderImg" alt="">
                                        </span>
                                        <p class="sender-name">{{ value.senderName }}</p>
                                    </a>
                                    <span class="message-date">{{ value.date }}</span>
                                    </span>
                                    <a href="javascript::" class="message-content-box">
                                        <h3 class="message-title">{{ value.title }}</h3>
                                        <p class="message-content">{{ value.content }}</p>
                                    </a>
                                </li>
                            </ul>
                            <a href="javascript:;" class="bottom-btn" @click.prevent="showMoreMessage">查看更多</a>
                        </div>

                        <!-- 没有消息 -->
                        <div v-if="messages.length === 0">
                            <p class="no-unread-message">您还没有未读消息</p>
                        </div>
                    </div>
                </a>
            </div>

            <!-- 点击登录注册时显示登录框 -->
            <login v-if="loginFlag" @closePopup="closePopup"></login>

            <!-- 点击修改资料时显示 修改资料弹窗 -->
            <modify v-if="modifyFlag" @showModifyPopup="showModifyPopup"></modify>

            <!-- 已保存 -->
            <small-pop :text="preserved" v-if="false"></small-pop>

            <!-- 点击上传作品 显示上传作品弹窗 -->
            <relase v-show="relaseFlag" @closeClick="closeRelasePopup"></relase>

            <!-- 点击退出登录 -->
            <exit v-if="exitFlag" @exitLogon="exitLogon" @closeRelasePopup="closeRelasePopup"></exit>

        </div>
        <div class="mask" v-show="flag"></div>
    </header>
</template>
<script>

    import Login from '../Popup/Login/login.vue';
    import Modify from '../Popup/Modify/modify.vue';
    import Relase from '../Popup/Relase/relase.vue';
    import Exit from '../Popup/Login/exit.vue'
    import SmallPop from '../component/smallPop.vue';
    export default {
        data() {
            return {
                topNavList: [ // 导航条的模拟数据
                    { navHref: "/recommend", text: "推荐" },
                    { navHref: "/picture", text: "图片" },
                    { navHref: "/video", text: "视频" },
                    { navHref: "/audio", text: "音频" },
                    { navHref: "/artist", text: "艺术家" }
                ],
                options: [ // 个人资料卡的模拟数据
                    { classType: "upload", title: "上传作品", event: this.showUploadPopup },
                    { classType: "edit", title: "修改资料", event: this.showModifyPopup },
                    { classType: "home", title: "我的主页", event: this.goToMine },
                    { classType: "wallet", title: "我的账户", event: this.goToAccount },
                    { classType: "attestation", title: "艺术家认证", event: this.goTocCertify },
                    { classType: "enterprise", title: "升级为企业号", event: this.goToUpgrade },
                ],
                messages: [ // 消息的模拟数据
                    { id: 1, reading: true, senderImg: "static/images_header/header2.jpg", senderName: "上帝的宠儿", date: "2018-8-10 18:20", title: "XX消息标题XX消息标题XX消息标题XX消息标题XX消息标题XX消息标题",
                    "content": "XX消息内容XX消息内X消息内容XX消X消息内容XX消X消息内容XX消容XX消息" },
                    { id: 2, reading: false, senderImg: "static/images_header/header4.jpg", senderName: "好好说话", date: "2018-12-10 20:22", title: "121212XX消息标题XX消息标题XX消息标题XX消息标题XX消息标题",
                    "content": "121212XX消息内容XX消息内X消息内容XX消X消息内容XX消X消息内容XX消容XX消息" }
                ],
                flag: false, // 显示黑色遮罩
                searchFlag: false, // 显示搜索框
                loginFlag: false, // 显示登录弹窗
                modifyFlag: false, // 显示修改资料弹窗
                relaseFlag: false, // 显示上传作品弹窗
                exitFlag: false, // 确认退出
                preserved: "已保存", // 修改资料后，点击确认按钮，显示以保存提示
            }
        },
        methods: {

            // 显示搜索框
            showSearch () {
                this.flag = true;
                this.searchFlag = true;
            },

            // 关闭搜索框
            closeSearch () {
                this.flag = !this.flag;
                this.searchFlag = false;
            },

            // 显示登录弹窗
            showLogin () {
                this.loginFlag = !this.loginFlag;
            },

            // 关闭登录弹窗
            closePopup (value) {
                this.loginFlag = value;
            },

            // 显示上传作品弹窗
            showUploadPopup () {
                this.relaseFlag = !this.relaseFlag;
            },

            // 关闭上传作品弹窗
            closeRelasePopup () {
                this.relaseFlag = !this.relaseFlag;
            },

            // 跳转至我的主页
            goToMine () {
                this.$router.push('/mine');
            },

            // 跳转至我的账户
            goToAccount () {
                this.$router.push('/account');
            },

            // 跳转至升级为企业账号
            goToUpgrade () {
                this.$router.push('/account/upgrade');
            },

            // 跳转至认证艺术家
            goTocCertify () {
                this.$router.push('/account/certify');
            },

            // 退出登录
            exitLogon () {
                this.exitFlag = !this.exitFlag;
            },

            // 关闭确认退出登录弹窗
            closeExitPopup () {
                this.$store.commit("ISLOGIN", !this.$store.state.isLogin);
            },

            // 修改个人资料
            showModifyPopup () {
                this.modifyFlag = !this.modifyFlag;
            },

            // 查看更多信息
            showMoreMessage () {
                console.log("hello world");
            }
        },
        components: {
            Login,
            Modify,
            Relase,
            Exit,
            SmallPop
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped> 

    /*路由高亮*/
    .router-link-exact-active {
        color: #48abff !important;
        border-bottom: 5px solid #48abff !important;
    }

    /*header部分*/
    .header {
        width: 100%;
        min-width: 800px;
        height: 54px;
        background-color: #2a2a2a;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 3;
        .container {
            max-width: 1040px;
            width: auto;
            height: 100%;
            margin: 0 auto;
            position: relative;
            .center {
                height: 100%;
                position: relative;
                .top-nav {
                    width: 334px;
                    height: 100%;
                    margin: 0 auto;
                    position: relative;
                    z-index: 1;
                    .item {
                        float: left;
                        margin: 0 18px;
                        a {
                            font-weight: bold;
                            color: #D6D6D6;
                            height: 54px;
                            line-height: 54px;
                            &:hover {
                                color: #48abff;
                            }
                        }
                    }
                }
                .search-wrap {
                        width: 610px;
                        height: 100%;
                        position: relative;
                        margin: 0 auto;
                        top: -54px;
                        left: 20px;
                        .search-icon {
                            width: 66px;
                            height: 100%;
                            background: url(../../../static/images_icon/sprite.png) no-repeat #48abff;
                            background-position: -106px -95px;
                            background-size: auto 300px;
                            position: absolute;
                            top: 0;
                            right: 66px;
                            z-index: 0;
                        }
                        .search-click {
                            background-color: #fff !important;
                            background-position: -106px -53px;
                            z-index: 1;
                        }

                        .search-close {
                            width: 66px;
                            height: 100%;
                            background: url(../../../static/images_icon/sprite.png) no-repeat;
                            background-position: -111px -134px;
                            background-size: auto 300px;
                            position: absolute;
                            top: 0;
                            right: 0;
                            z-index: 2;
                            cursor: pointer;
                        }
                        .search-input {
                            width: 480px;
                            height: 100%;
                            position: absolute;
                            z-index: 1;
                            padding-left: 18px;
                            font-size: 16px;
                            font-weight: bold;
                            color: #343434;
                        }
                }
            }
            .logo {
                height: 32px;
                width: 98px;
                position: absolute;
                left: 0;
                top: 10px;
            }
            .login-or-register {
                display: inline-block;
                width: 74px;
                position: absolute;
                right: 12px;
                top: 0;
                font-size: 14px;
                font-weight: bold;
                color: #D6D6D6;
                height: 54px;
                line-height: 54px;
            }

            /*个人帐户*/
            .account-box {
                position: absolute;
                top: 0;
                right: 0;
                width: auto;
                height: 100%;
                .hover-box {
                    width: 174px;
                    border-radius: 6px;
                    background: #fff;
                    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
                    padding-top: 2px;
                    position: absolute;
                    top: 52px;
                    right: -20px;
                    display: none;
                    .bottom-btn {
                        height: 42px;
                        border-radius: 0 0 6px 6px;
                        padding: 0 8px;
                        overflow: hidden;
                        border-top: 1px solid #EAEAEA;
                        line-height: 42px;
                        color: #48ABFF;
                        font-weight: bold;
                        text-align: center;
                        &:hover {
                            background-color: #48ABFF;
                            border-color: #48ABFF;
                            color: #E5F5FF;
                        }
                    }
                    &:before {
                        content: '';
                        display: block;
                        height: 0;
                        width: 0;
                        border: 6px solid #fff;
                        border-color: transparent transparent #fff transparent;
                        position: absolute;
                        top: -12px;
                        right: 58px;
                    }
                }
                .account-head-box {
                    width: 33px;
                    height: 100%;
                    float: left;
                    margin-right: 28px;
                    padding-top: 10px;
                    .account-head {
                        width: 33px;
                        height: 33px;
                        border-radius: 50%;
                        overflow: hidden;
                        &:hover {
                            .account-option {
                                display: block;
                                z-index: 1;
                            }
                            .perfect-information-tip {
                                display: none;
                            }
                        }
                        .account-option {
                            left: -92px;
                            .list {
                                .item {
                                    a {
                                        width: 100%;
                                        height: 39px;
                                        margin: 5px 0;
                                        .icon {
                                            width: 27px;
                                            height: 100%;
                                            float: left;
                                            margin-left: 12px;
                                            background: url(../../../static/images_icon/sprite.png) no-repeat;
                                            background-size: auto 300px;
                                        }
                                        .upload {
                                            background-position: -179px -105px;
