                            padding-top: 0;
                            display: block;
                            &:before {
                                border-color: transparent transparent #FF6448 transparent;
                            }
                            .perfect-information-text {
                                width: 100%;
                                height: 100%;
                                line-height: 32px;
                                color: rgba(255, 255, 255, 0.9);
