                            }
                        }
                    }
                }
                .account-type {
                    width: 46px;
                    height: 20px;
                    position: absolute;
                    top: 8px;
                    left: -46px;
                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                    background-position: 1px -33px;
                    background-size: auto 292px;
                }
                
                /*消息*/
                .message {
                    float: left;
                    width: 20px;
                    background: url(../../../static/images_icon/sprite.png) no-repeat;
                    background-position: -59px -80px;
