                            color: #adabab;
                        }
                        .list {
                            padding: 0 10px;
                            overflow: hidden;
                            .item {
                                border-top: 1px solid #EAEAEA;
                                padding-top: 15px;
                                &:first-child {
                                    border-top: none;
                                }
                                .message-author-box {
                                    height: 23px;
                                    overflow: hidden;
                                    float: left;
                                    width: auto;
                                    position: relative;
                                    padding-left: 13px;
                                    .unread {
                                        width: 6px;
                                        height: 6px;
                                        position: absolute;
                                        top: 7px;
                                        left: 0;
                                        border-radius: 50%;
                                        background: #fe6448;
                                    }
                                    .sender-head {
                                        display: block;
                                        float: left;
                                        width: 23px;
                                        height: 23px;
                                        border-radius: 50%;
                                        overflow: hidden;
                                        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
                                    }
                                    .sender-name {
                                        float: left;
                                        margin-left: 6px;
                                        font-size: 14px;
                                        color: #2E2E2E;
                                        height: 100%;
                                        line-height: 23px;
