<template>
    <div>
        <popup>
            <div slot="content">
                <!-- 弹框标题 -->
                <h2 class="header">
                    <span class="edit-icon"></span>
                    <p class="text">修改资料</p>
                </h2>
                <!-- 弹框内容 -->
                <div class="slide-box">

                    <form class="modify-form">
                        <div class="form-control">
                            <label class="label-title">昵称：</label>
                            <input class="input-control" type="text" name="username" placeholder="输入您的昵称" />
                        </div>
                        <div class="form-control">
                            <label class="label-title">一句话自我介绍：</label>
                            <input type="text" placeholder="输入一句话自我介绍" class="input-control w245" />
                        </div>
                        <div class="form-control">
                            <label class="label-title">技能：</label>
                            <input type="text" v-model="skill" placeholder="输入您的技能" class="input-control" />
                            <o-button :text="'添加'" :classStyle="'main-btn'" class="add-label-btn" @btnClick="addSkill(skill)"></o-button>
                        </div>
                        <div class="labels">
                            <ul class="labels-list">
                                <li class="item" v-for="(value, index) in skills" :key="index">{{ value }}</li>
                            </ul>
                            <span class="com-labels-title">常用技能：</span>
                            <ul class="labels-list">
                                <li class="item" v-for="(value, index) in comSkills" :key="index">{{ value }}</li>
                            </ul>
                        </div>
                        <div class="form-control">
                            <label class="label-title">性别：</label>
                            <select class="select-control">
                                <option value ="man">男</option>
                                <option value ="girl">女</option>
                            </select>
                        </div>
                        <div class="form-control">
                            <label class="label-title">出生日期(阳历)：</label>
                            <div class="birth-date">
                                <select class="select-control">
                                    <option :value ="item" v-for="item in years" :key="item">{{ item }}</option>
                                </select>
                                <span class="date">年</span>
                                <select class="select-control">
                                    <option :value ="item" v-for="item in months" :key="item">{{ item }}</option>
                                </select>
                                <span class="date">月</span>
                                <select class="select-control">
                                    <option :value ="item" v-for="item in days" :key="item">{{ item }}</option>
                                </select>
                                <span class="date">日</span>
                            </div>
                        </div>
                        <div class="form-control city">
                            <label class="label-title">所在城市：</label>

                            <v-distpicker></v-distpicker>
                        </div>
                        <div class="form-control">
                            <label class="label-title">家乡：</label>
                            <v-distpicker only-province></v-distpicker>
                        </div>
                        <div class="form-control">
                            <label class="label-title">毕业学校：</label>
                            <input type="text" placeholder="输入您毕业学校的名称" class="input-control" />
                            <input type="text" placeholder="输入您的专业" class="input-control w142" />
                        </div>
                        <div class="form-control">
                            <label class="label-title">作品链接：</label>
                            <input type="text" placeholder="输入您线上的作品链接，方便别人更了解您" class="input-control w320" />
                        </div>
                        <o-button :text="'确定'" :classStyle="'main-btn'" class="btn" @btnClick="makeSure"></o-button>
                    </form>
                </div>
            </div>
            <input type="button" name="" class="close-btn" slot="close" @click="closePopup" />
        </popup>

        <!-- 保存资料成功 -->
        <small-pop :text="'保存'" v-if="preserFlag" class="preserPop"></small-pop>
    </div>
    
</template>
<script>

    // 引入省市级联动插件
    import VDistpicker from 'v-distpicker';
    import OButton from '../../component/button.vue';
    import Popup from '../../component/popup.vue';
    import SmallPop from '../../component/smallPop.vue';

    export default {
        data() {
            return {
                select: { province: '广东省', city: '广州市' },
                skill: '',
                skills: ["风景阿姆", "山水中国", "山川河流", "风景"],
                comSkills: ["人物", "中国", "河流", "风景"],
                years: [],
                months: [],
                days: [],
                preserFlag: false, // 保存成功弹窗
            }
        },
        created() {
            this.getYears();
            this.getMonths();
            this.getDays();

        },
        methods: {

            // 关闭弹窗
            closePopup () {

                this.$emit('showModifyPopup', )
            },

            // 添加技能
            addSkill(skill) {
                let that = this;

                // 判断是否有重复添加的技能
                if (that.skills.indexOf(skill) === -1) {
                    that.skills.push(skill);
                }
            },

            // 获取年份数据
            getYears () {

                for (let i = 1980; i <= 2020; i++) {
                    this.years.push(i);
                };

                return this.years;
            },

            // 获取月份数据
            getMonths () {

                for (let n = 1; n <= 12; n++) {
                    this.months.push(n);
                }

                return this.months;
            },

            // 获取日数据
            getDays () {

                for (let m = 1; m <= 31; m++) {
                    this.days.push(m);
                }

                return this.days;
            },

            // 单击确定后显示保存
            makeSure () {
                this.preserFlag = !this.preserFlag;
                let _this = this;

                // 800ms 后消失
                setTimeout(function () {
                    _this.closePopup();
                }, 800);
            }
        },
        components: {
            VDistpicker,
            OButton,
            Popup,
            SmallPop
        }
    }
</script>
<style lang="scss" rel="stylesheet/scss" scoped media="screen">
    
    /*发布弹框*/
    .header {
        height: 43px;
        padding: {
            top: 18px;
            left: 20px;
        }
        .edit-icon {
            width: 20px;
            height: 20px;
            margin-top: 2px;
            background-color: antiquewhite;
            float: left;
            background: url(/static/images_icon/sprite.png) center center no-repeat;
            background-position: -3px -213px;
            background-size: auto 300px;
        }
        .text {
            float: left;
            margin-left: 10px;
            font: {
                size: 18px;
                weight: bold;
            }
        }
    }

    .slide-box {
        width: 540px;
        padding: 0 30px;
        min-height: 397px;
        height: 500px;
        overflow-y: auto;
        transition: all 0.3s cubic-bezier(.07, .63, .39, .98);

        // 修改个人资料表单
        .modify-form {
            .btn {
                margin: 30px auto;
                width: 78px;
                height: 34px;
            }
            .form-control {
                width: 94%;
                height: 32px;
                margin: 20px auto 0;
                overflow: hidden;
                .label-title {
                    width: 108px;
                    text-align: right;
                    float: left;
                    line-height: 32px;
                    font: {
                        size: small;
                        weight: bold;
                    }
                }
                
                .input-control {
                    display: block;
                    width: 168px;
                    height: 100%;
                    border-radius: 4px;
                    float: left;
                    background-color: #F4F4F4;
                    padding: 0 6px;
                    font-size: 13px
                }
                .w320 {
                    width: 320px;
                }
                .w245 {
                    width: 245px;
                }
                .w245 {
                    width: 245px;
                }
                .w142 {
                    width: 142px;
                    margin-left: 10px;
                }
                .select-control {
                    display: block;
                    width: 58px;
                    height: 100%;
                    border: none;
                    background-color: #F4F4F4;
                    padding: 0;
                    float: left;
                }
                .add-label-btn {
                    width: 58px;
                    height: 100%;
                    float: left;
                    font-size: 13px;
                    margin-left: 6px;
                }
            }
            .labels {
                margin-left: 121px;
                .labels-list {
                    margin-top: 14px;
                    overflow: hidden;
                    .item {
                        font-size: smaller;
                        float: left;
                        line-height: 20px;
                        border-radius: 2px;
                        border: 1px solid #d6d6d6;
                        margin: {
                            right: 10px;
                            bottom: 10px;
                        }
                        padding: 0 8px;
                    }
                }
                .com-labels-title {
                    font-size: 12px;
                    color: #626262;
                    font-weight: normal;
                    margin-top: 10px;
                }
            }

            // 出生日期
            .birth-date {
                height: 100%;
                .select-control {
                    float: left;
                    height: 100%;
                    font-size: smaller;
                    line-height: 32px;
                    padding: 0 8px;
                }
                span.date {
                    height: 100%;
                    float: left;
                    font-size: 14px;
                    line-height: 32px;
                    padding: 0 8px;
                }
            }
        }
    }

    .preserPop {
        z-index: 3;
    }
</style>


