<template>
    <div class="mask popup-mask">
        <div class="popup">
            {{ text }}
        </div>
    </div>
</template>
<script>

    export default {
        data () {
            return {
            }
        },
        props: {
            text: {
                type: [String],
                default: "提示信息"
            }
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" media="screen" scoped>

    $width: 130px;

    .popup-mask {
        background: rgba(0, 0, 0, 0.15);
        position: fixed;
    }

    .popup {
        width: $width;
        height: 36px;
        background: #fff;
        border-radius: 3px;
        line-height: 3;
        text-align: center;
        font: {
            size: x-small;
            weight: bold;
        }
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translateX(-$width / 2);
        z-index: 3;
        box-shadow: 0 3px 30px rgba(0, 0, 0, 0.15);
    }
</style>


